.container {
    width: 100%;
    padding: 2em 3em;
    display: flex;
    flex-direction: column;
}

.box {
    background-color: var(--grey);
    border: solid 2px var(--border);
    border-radius: 1rem;
    min-height: 100px;
    min-width: 200px;
}

.box>h4 {
    margin: 0;
    padding: .2em;
    font-size: 20px;
    font-weight: 400;
    text-align: center;
    border-bottom: solid 1px var(--border);
}

.box>div {
    padding: .5em;
    max-height: var(--item-max-h);
}


.item {
    max-height: 30px;
    border: solid 1px var(--border);
    border-radius: .5em;
    padding: 5px;
    cursor: pointer;
}

:root {
    --item-max-h: 30px;
}