
.menu {
    max-width: 800px;
    display: flex;
    background-color: rgb(32, 32, 32);
    position: relative;
}   

.box {
    min-height: 50px;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: solid 2px rgb(14, 14, 14);
    border-radius: 3px;
    cursor: pointer;
}

.box:first-child {
    border-right: none;
}

.active {
    border-bottom: solid 2px var(--gold);
    color: var(--gold);
}
